@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700);
* {
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

.mr-20 {
  margin-right: 20px !important;
}



.App {
    height: 100vh;
    display: flex;
    color: white;
  }
  
  .App__Aside {
    width: 50%;
    background-color: #66DAC7;
  }
  
  .Secure {        
    background-color: none;
    width:100%;
  }
  .App__Form {
    width: 50%;
    background-color: #2E4158;
    padding: 25px 40px;
    overflow: auto;
    
  }
  
  .PageSwitcher {
    display: flex;
    justify-content: flex-end;
    margin-bottom: 10%;
  }
  
  .PageSwitcher__Item {
    background-color: #4C5D72;
    color: #9DA6B1;
    padding: 10px 25px;
    cursor: pointer;
    font-size: .9em;
    border: none;
    outline: none;
    display: inline-block;
    text-decoration: none;
  }
  
  .PageSwitcher__Item--Active {
    background-color: #5ED0C0;
    color: white;
  }
  
  .PageSwitcher__Item:first-child {
    border-top-left-radius: 25px;
    border-bottom-left-radius: 25px;
  }
  .PageSwitcher__Item:last-child {
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
  }
  
  .FormCenter {
    margin-bottom: 100px;
  }
  
  .FormTitle {
    color: #707C8B;
    font-weight: 300;
    margin-bottom: 50px;
  }
  
  .FormTitle__Link {
    color: #707C8B;
    text-decoration: none;
    display: inline-block;
    font-size: 1.7em;
    margin: 0 10px;
    padding-bottom: 5px;
  }
  
  .FormTitle__Link:first-child {
    margin-left: 0;
  }
  
  .FormTitle__Link--Active {
    color: white;
    border-bottom: 1px solid #199087;
  }
  
  .FormField {
    margin-bottom: 40px;
  }
  
  .FormField__Label {
    display: block;
    text-transform: uppercase;
    font-size: .9em;
    color: white;
  }
  .FormField__Input {
    width: 85%;
    background-color: transparent;
    border: none;
    color: white;
    outline: none;
    border-bottom: 1px solid #445366;
    font-size: 1em;
    font-weight: 300;
    padding-bottom: 10px;
    margin-top: 10px;
  }
  
  .FormField__Input::-webkit-input-placeholder {
    color: #616E7F;
  }
  
  .FormField__Input:-ms-input-placeholder {
    color: #616E7F;
  }
  
  .FormField__Input::-ms-input-placeholder {
    color: #616E7F;
  }
  
  .FormField__Input::placeholder {
    color: #616E7F;
  }
  
  .FormField__Button {
    background-color: #52C4B9;
    color: white;
    border: none;
    outline: none;
    border-radius: 25px;
    padding: 15px 70px;
    font-size: .8em;
    font-weight: 500;
  }
  
  .FormField__Link {
    color: #66707D;
    text-decoration: none;
    display: inline-block;
    border-bottom: 1.5px solid #225E62;
    padding-bottom: 5px;
  }
  
  .FormField__CheckboxLabel {
    color: #646F7D;
    font-size: .9em;
  }
  
  .FormField__Checkbox {
    position: relative;
    top: 1.5px;
  }
  
  .FormField__TermsLink {
    color: white;
    border-bottom: 1px solid #199087;
    text-decoration: none;
    display: inline-block;
    padding-bottom: 2px;
    margin-left: 5px;
  }
  
  .image {
    
    position: absolute;
    border-radius: 1.5rem;
    border: 1px solid rgb(70, 71, 66);
    display: none;
  }
.img {
	max-width: 80px;
	max-height: 80px;
	margin-bottom: -9px;
	border-radius: 50px;
	border-color: black;
	-webkit-transition-duration: 0.8s;
	        transition-duration: 0.8s;
	border-style: double;
}

nav {
	width: 100%;
	margin-left: 20px;
	background: #fff;
	padding: 0;
	box-shadow: 0px 5px 0px #dedede;
	box-sizing: border-box;
}
nav ul li {
	display: inline-block;
}
nav ul li button {
	padding: 5px;
	color: black;
	font-weight: 900;
	font-size: 15px;
	margin: 15px;
	background-color: transparent;
	border-color: transparent;
	outline-color: transparent;
	-webkit-transition-duration: .3s;
	        transition-duration: .3s;
	font-family: Arial;
}

nav ul li button:hover {
	color: black;
}
nav.fill ul li button {
	position: relative;
}
nav.fill ul li button:after {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	margin: auto;
	width: 0%;
	content: '.';
	color: transparent;
	background: teal;
	height: 1px;
}
nav.fill ul li button {
	-webkit-transition: all 2s;
	transition: all 2s;
}

nav.fill ul li button:hover {
	color: white;
	z-index: 1;
}
nav.fill ul li button:hover:after {
	z-index: -10;
	animation: fill 1s forwards;
	-webkit-animation: fill 1s forwards;
	-moz-animation: fill 1s forwards;
}

@-webkit-keyframes fill {
	0% {
		width: 0%;
		height: 1px;
	}
	50% {
		width: 100%;
		height: 1px;
		border-radius: 5px;
	}
	100% {
		width: 100%;
		height: 100%;
		background: teal;
		opacity: 1;
		border-radius: 5px;
	}
}

